.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40px;
}

.App-header {
    background-color: #000;
    height: 70px;
    padding: 10px;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-size: 1.4em;
}

.App-title {
    font-size: 1.5em;
}

.App-intro {
    font-size: large;
}

.main {
    background-color: black;
    height: 100%;
    font-family: 'Roboto', sans-serif;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.topInfo {
    display: grid;
    grid-template-rows: 50px 35px;
    grid-template-columns: 3fr 2fr 3fr;
    -ms-flex-pack: center;
        justify-content: center;
    justify-items: stretch;
}

.topControls {
    grid-area: 1/2/2/3;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.controlButtons {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.gens {
    color: #ff0099;
    font-size: 1.3em;
    grid-area: 2/2/3/3;
    margin-top:12px;
}

.buttons {
    padding: 5px 13px 5px 13px;
    background-color: rgba(0, 0, 0, 0);
    border: dashed 3px white;
    margin: 5px;
    border-radius: 3px;
    cursor: pointer;
}

.play {
    color: #39ff14;
}

.pause {
    color: #fffe00;
}

.reset {
    color: red;
}

.speedControls {
    display: grid;
    grid-template-columns: 4fr 1fr 1fr 1fr 4fr;
    grid-gap: 5px;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.speed {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row no wrap;
        flex-flow: row no wrap;
    -ms-flex-pack: center;
        justify-content: center;
    color: #39ff14;
    font-size: 1.1em;
}

.speed1,
.small {
    grid-column: 2/3;
}

.speed2,
.medium {
    grid-column: 3/4;
}

.speed3,
.fast {
    grid-column: 4/5;
}

.speed1 {
    color: #fffe00
}

.speed3 {
    color: white
}

.sizeControls {
    display: grid;
    grid-template-columns: 4fr 1fr 1fr 1fr 4fr;
    grid-gap: 5px;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.size {
    font-size: 1.1em;
}

.small {
    color: #ff6600
}

.medium {
    color: #00f9ff
}

.large {
    color: brown;
}

.gridWrapper {
    display: grid;
    justify-items: stretch;
    margin: 20px 0 20px 0;
    width: 100%;
    grid-template-columns: 4fr 7fr 4fr;
}

.grid {
    display: grid;
    grid-column: 2/3;
    grid-auto-rows: 1fr;
    height: 45vw;
}

@media (max-aspect-ratio: 1/1) {
    .gridWrapper {
        display: grid;
        justify-items: stretch;
        margin: 20px 0 20px 0;
        width: 100%;
        grid-template-columns: 1fr 12fr 1fr;
    }
    .grid {
        display: grid;
        grid-column: 2/3;
        grid-auto-rows: 1fr;
        height: 86vw;
    }
}

@media (min-aspect-ratio: 16/10) {
    .gridWrapper {
        display: grid;
        justify-items: stretch;
        margin: 20px 0 20px 0;
        width: 100%;
        grid-template-columns: 4fr 5fr 4fr;
    }
    .grid {
        display: grid;
        grid-column: 2/3;
        grid-auto-rows: 1fr;
        height: 38vw;
    }
}

.row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
}

.cell {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-positive: 1;
        flex-grow: 1;
    height: 100%;
    border: 0.5px solid rgba(38, 55, 246, 0.507);
}

.alive-true {
    background-color: #fffe00;
}

.alive-false {
    background-color: #000;
}

.alive-true-old {
    background-color:  #ffc400;
}

.alive-true-mature {
    background-color: rgb(255, 0, 0);
}



